export default {
	data() {
		return {
			timer: 0,

		};
	},
	props:{
		contentHeight:{
			type: Number,
			default: 0
		},
		//是否是tabbar页面
		hasTabbar:{
			type: Boolean,
			default: false
		},
		shareList:{
			type: Array,
			default: []
		},
		showShare:{
			type:Boolean
		}
	},
	onLoad() {
	},
	methods:{
		toggleMask(){
			//防止高频点击
			if(this.timer == 1){
				return;
			}
			this.timer = 1;
			setTimeout(()=>{
				this.timer = 0;
			}, 500)	
		},
		//防止冒泡和滚动穿透
		stopPrevent(){},
		//分享操作
		shareToFriend(index){
			this.$emit("click",index)
		},
	}
}
